<template>
    <div>
        <v-header></v-header>
        <ul class="nav">
            <li v-for="item in arrList" :key="item.id">
                <img :src="item.imgURL" alt="">
                <span>{{item.title}}</span>
            </li>
        </ul>
        <v-course :arrList="array"></v-course>
        <v-footer></v-footer>
    </div>
</template>
<script>
import Header from "@/components/header/"
import Footer from '@/components/footer/'
import Web from "@/assets/web.png"
import Move from "@/assets/move.png"
import Data from "@/assets/data.png"
import WebService from "@/assets/webservice.png"
import Course from '@/components/course/'

import course1 from '@/assets/course1.jpg'
import course2 from '@/assets/course2.jpg'
import course3 from '@/assets/course3.jpg'
import course4 from '@/assets/course4.jpg'
import course5 from '@/assets/course5.jpg'

export default {
    data() {
        return {
            arrList:[],
            array:[]
        }
    },
    mounted(){
         this.arrList = [
             {imgURL:Web,title:'电竞时事'},
             {imgURL:Move,title:'社区事件'},
             {imgURL:Data,title:'大数据'},
             {imgURL:WebService,title:'新游评测'},
         ];
         this.array = [
             {imgURL:course1,title:'电竞究竟是毒药还是爱好？',msg:'中型 · 330人在讨论'},
             {imgURL:course2,title:'游戏版号约束是不是一件好事',msg:'大型 · 550人在讨论'},
             {imgURL:course3,title:'用这三个套路98%能吃鸡',msg:'小型 · 27人在讨论'},
             {imgURL:course4,title:'直播与电竞的相爱相杀',msg:'中型 · 280人在讨论'},
             {imgURL:course5,title:'阿里会不会布局电竞行业？',msg:'中型 · 260人在讨论'},
             {imgURL:course5,title:'买断制和充值制的选择',msg:'中型 · 260人在讨论'},
             {imgURL:course5,title:'你支持父辈和你一起游戏吗',msg:'大型 · 700人在讨论'},
         ]
    },
    components:{
        'v-header':Header,
        'v-course':Course,
        'v-footer':Footer
    }
}
</script>
<style scoped>
    .nav{
		height: 100px;
		padding-top: 44px;
		background: #F3F5F7;
		overflow-x: scroll;
		white-space: nowrap;
		font-size: 0;
	}
	.nav li{
		width: 88px;
		display: inline-block;
		text-align: center;
		font-size: 14px;
	}
	.nav li img{
		display: block;
		width: 32px;
		margin: 20px auto 12px;

	}
</style>


